<template>
  <ul :class="{ 'sp-menu-small': !menuStatus }">
    <li class="sp-menu" v-if="menus" v-for="menu in menus" :key="menu.uid" :style="{'background-color': backgroundColor}">
      <div class="sp-title" :class="[{active: menu.active, 'sp-arrow': menu.children && !menu.url}, {'is-active': selectId == menu.uid}]" @click="clickMenu(menu)" 
        :style="{'padding-left': pdleft + 'px','color': activeMenuColor[0] && selectId == menu.uid ? activeMenuColor[1] : menuColor, 'backgroundColor': selectId == menu.uid ? hoverBgColor : backgroundColor, 'height': height, 'line-height':height}">
        <div class="bg-hove" :style="{'background-color': hoverBgColor}"></div>
        <i class="iconfont" v-if="menu.icon" :class="menu.icon"></i>
        <span :class="{'is-hover': !menuStatus && isUpNav && !menu.children}">{{ menu.label }}</span>
      </div>
      <sp-collapse-transition>
        <sp-menu v-show="menu.children && menu.active" @select-id="getSelectId" @page-config="getPageConfig"  @open="menuOpen" @close="menuClose" 
          :menus="menu.children" 
          :backgroundColor="backgroundColor" 
          :hoverBgColor="hoverBgColor" 
          :menuColor="menuColor" 
          :activeMenuColor="activeMenuColor" 
          :accordion="accordion" 
          :width="width" 
          :height="height" 
          :menuStatus="menuStatus" 
          :router="router" 
          :selectId="selectId" 
          :isUpNav="false" 
          :paddingLeft="pdleft"
          :pagePermissions="pagePermissions"
        :style="{'width': width, 'z-index': zIndex(menu)}"/>
      </sp-collapse-transition>
    </li>
  </ul>
</template>

<script>
export default {
    name: 'sp-menu',
    props: {
      //
      menus: {
        type: Array,
        default: function() {
          return []
        },
      },
      backgroundColor: {//
        type: String,
        default: '#2e323e'
      },
      hoverBgColor: {//hove
        type: String,
        default: '#409eff'
      },
      menuColor: {//
        type: String,
        default: '#fff'
      },
      activeMenuColor: {//
        type: Array,
        default: function() {
          return [false, '#fff']
        }
      },
      accordion: {//
        type: Boolean,
        default: true
      },
      width: {//menu
        type: String,
        default: '200px'
      },
      height: {//menu
        type: String,
        default: '36px'
      },
      menuStatus: {//
        type: Boolean,
        default: true
      },
      router: {//router
        type: Boolean,
        default: false
      },
      pagePermissions: {//
        type: Boolean,
        default: false
      },
      selectId: {//ID
        type: String,
        default: ''
      },
      isUpNav: {//
        type: Boolean,
        default: true
      },
      paddingLeft: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
      }
    },
    computed: {
      //padding-left
      pdleft() {
        return this.paddingLeft + 20;
      },
    },
    methods:{
      //menu
      clickMenu(menu) {
        let config = menu.configs ? menu.configs : [],
          l = this.menus.length;
        if (!menu.url){
          if ( this.menuStatus) {
            menu.active = !menu.active;
            menu.active ? this.$emit('open', menu.uid) : this.$emit('close', menu.uid);
            if (this.accordion) {//
              for (let i = 0; i < l; i++) {
                if (menu.active) {
                  if (this.menus[i].uid != menu.uid) {
                    if (this.menus[i].active) {
                      this.menus[i].active = false;
                      this.$emit('close', this.menus[i].uid);
                    }
                    menu.uid.split('-').length == 1 && this.menus[i].children && this.navClose(this.menus[i].children);
                  }
                }
              }
            }
          }
        } else {
          this.$emit('select-id', menu.uid);
          this.pagePermissions && this.$emit('page-config', config);
          sessionStorage.selectId = menu.uid;
          sessionStorage.configs = JSON.stringify(config);
          this.router ? this.$router.push(menu.url) : window.location.href = menu.url;
          // if(this.accordion) {//
          //   let l = this.menus.length;
          //   for (let i = 0; i < l; i++) {
          //     if(this.menus[i].uid != menu.uid) {
          //       this.menus[i].active = false;
          //       menu.uid.split('-').length == 1 && this.menus[i].children && this.navClose(this.menus[i].children);
          //     }
          //   }
          // }
        }
      },
      // 
      navClose(array) {
        for (let i = 0; i < array.length; i++) {
          let arr = array[i].children;
          if(this.accordion) {}
          if(array[i].active == true) {
            array[i].active = false 
          }
          arr && this.navClose(arr);
        }
      },
      zIndex(menu) {
        let l = menu.uid.split('-').length;
        return 1000 - l*2;
      },
      menuOpen(val) {
        this.$emit('open', val)
      },
      menuClose(val) {
        this.$emit('close', val);
      },
      //ID
      getSelectId(val) {
        this.$emit('select-id', val);
      },
      //
      getPageConfig(val) {
        this.pagePermissions && this.$emit('page-config', val);
      },
    }
}
</script>

<style lang="scss" scoped>
@import '../../../static/icon-font/iconfont.css';
.bg-hove{
  position: absolute;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 1;
}
.sp-menu{
  position: relative;
  .sp-title{
    font-size: 14px;
    padding: 0 20px;
    z-index: 0;
    cursor: pointer;
    position: relative;
    transition: border-color .3s,background-color .3s,color .3s;
    box-sizing: border-box;
    white-space: nowrap;
    span{
      text-decoration: none;
      display: inline-block;
    }
    span, i{
      position: relative;
      z-index: 5;
      opacity: .7;
      transition: opacity .1s ease;
    }
    .bg-hove{
      opacity: 0;
    }
    &:hover{
      .bg-hove{
        opacity: 1;
      }
      span, i{
        opacity: 1;
      }
      .is-hover{
        left: 6px;
        width: auto;
        padding: 0 10px;
        background-color: #2e323e;
        color: #fff;
        position: relative;
        z-index: 100;
        &:before{
          content: '';
          position: absolute;
          left: -2px;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 0;
          height: 0;
          border-top: 8px solid transparent;
          border-right: 8px solid #2e323e;
          border-bottom: 8px solid transparent;
        }
      }
    }
    &.is-active{
      span, i{
        opacity: 1;
      }
    }
  }
  //css
  .sp-arrow{
    &:after{
      content: '';
      display: block;
      width: 6px;
      height: 6px;
      border-left: 1px solid;
      border-bottom: 1px solid;
      position: absolute;
      z-index: 5;
      top: 38%;
      right: 15px;
      opacity: .7;
      transform: rotate(-45deg);
      transition: transform .3s ease-out,top .3s ease-out;
    }
    //css3
    &.active{
      &:after {
        transform: rotate(-225deg);
        top: 45%;
        transition: all .3s ease-in-out;
      }
    }
    &.hover{
      &:after {
        opacity: 1;
      }
    }
  }
}
.iconfont {
    vertical-align: middle;
    margin-right: 12px;
    font-size: 18px;
    width: 24px;
    text-align: center;
}
//
.sp-menu-small{
  >.sp-menu{
    overflow: hidden;
    >.sp-menu-small{
      display: none;
      transition: all .3s;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.2);
      margin-left: 1px;
    }
    &:hover{
      overflow: visible;
      >.sp-menu-small{
        display: block !important;
      }
    }
    >ul{
      position: absolute;
      top: 0;
    }
    
  }
}
//  
.sp-menu-group >.sp-menu-small{
  >.sp-menu{
    >.sp-title{
      padding: 0 12px !important;
    }
    >.sp-arrow:after, >a, >span{
      display: none;
      opacity: 0;
    }
    ul{
      left: 100%;
    }
    .sp-arrow:after{
      transform: rotate(-135deg);
      top: 13px;
    }
  }
}
</style>



